// import './Css/Home.css';
import React, { useState } from 'react'
import HeaderLogo from '../images/Sha7enLogo_White_200.png'
import Header_img_1 from '../images/Header_img_1.png'
import { Link } from 'react-router-dom'
import { BsLightningCharge } from "react-icons/bs";
import { useTranslation } from "react-i18next";
import i18next from 'i18next'
import cookies from 'js-cookie'


const Home = () => {
    const { t } = useTranslation()

    const partners_image = "data:image/png;base64,"

    return (
        <>
            <div className="home_Header page_padding">
                <div className="left_Header">
                    <div className="text_align_left"><img className="header_logo" src={HeaderLogo} width="200" alt='logo' /></div>
                    <h1 
                    className="header_slogan"
                    >
                        {t(' Your connection to charge')}
                        <BsLightningCharge className='iconbattery' />

                        </h1>

                    <p
                        className="header_description"
                    >
                        <b style={{ color: '#fff', fontSize: '30px' }}> { t('We')} </b> { t('are a supportive application that works as a connector between the customers and the charger providers ...')}
                    </p>
                    <Link
                        id="BookNow"
                        className="text_align_left animation btn btn__secondary h2__text"
                        to="/sha7en/Home"
                    >
                        {t('Book Now')}
                    </Link>
                </div>
                <div className="right_Header">
                    <div className="text_align_right"><img className="header_img" src={Header_img_1} alt='logo' />
                    </div>
                </div>
            </div>
            <div className="home_vehicles page_padding ">
                <h1 className="unbold black_font vehicles_title">{t('OUR PARTNERS')}</h1>
                <div className="vehicles_container">
                    <div className="partners_container">
                        <img className="partners_image general_shadow" src={partners_image} width="" alt='logo' />
                        <img className="partners_image general_shadow" src={partners_image} width="" alt='logo' />
                        <img className="partners_image general_shadow" src={partners_image} width="" alt='logo' />
                    </div>

                </div>

            </div>

        </>
    );
}

export default Home;